@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    box-sizing: border-box;
  }
  
  body{
    background-color: #e0aaff;
  }
  .App{
    /* background-color: #3f3160; */
    height: auto;
    width: 600px;
    margin: 40px auto;
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
  }

  .App h1{
    color: #240046;
    font-size: 2.5rem;
  }
  
  header{
    width: auto;
    min-height: 60px;
    background-color: #7b2cbf;
    position: relative;
    box-shadow: 5px 5px 5px #240046;
  }
  header #to-do-form textarea{
    background-color: #7b2cbf;
    border: 0;
    width: 75%;
    font-size: 18px;
    color: #fff;
    resize: none;
    overflow: hidden;
    font-family: 'Poppins', sans-serif;
    margin-top: 15px;
    margin-bottom: 10px;
    margin-left: 30px;
    margin-right: 30px;
  }
  #to-do-form textarea::-webkit-input-placeholder{
    color: #e0aaff;
  }
  #to-do-form textarea:-ms-input-placeholder{
    color: #e0aaff;
  }
  #to-do-form textarea::placeholder{
    color: #e0aaff;
  }
  #to-do-form textarea:focus, #to-do-form button:focus{
    outline: none;
  }
  #to-do-form button{
    height: 100%;
    width: 15%;
    border: 0;
    background-color: #7b2cbf;
    cursor: pointer;
    font-weight: bolder;
    color: #fff;
    position: absolute;
    right: 0;
  }

  .faicons{
    font-size: 22px;
  }

  .activity-list{
   margin-top: 25px;
   box-shadow: 5px 5px 5px #240046;
  }

  .last{
    text-align: center;
    color: #240046;
  }

  @media screen and (max-width:767px) {
    .App{
        width: 90%;
    }
    .last{
      margin-top: 90px;
    }
}
*{
    box-sizing: border-box;
}
.list{
    color: #fff;
    width: 100%;
    background-color: #FFF;
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    margin-bottom: 1px;
    position: relative;
}
.bin{
    margin: 0 10px;
    font-size: 20px;
    color: #7b2cbf;
}
.list textarea{
    border: 0;
    color: #240046;
    font-size: 18px;
    font-family: 'Poppins', sans-serif;
    resize: none;
    overflow: hidden;
    width: 80%;
    height: 100%;
}
.list textarea:focus{
    outline: none;
}

.list .circle {
    height: 26px;
    width: 26px;
    border-radius: 50%;
    border: solid 2px #7b2cbf;
    position: relative;
    margin: 0 10px;
  }
  
  .list .circle .innerCircle {
    border-radius: 50%;
    height: 16px;
    width: 16px;
    background-color:#7b2cbf;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}


  
.bottom{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    width: auto;
    min-height: 60px;
    background-color: #7b2cbf;
    font-size: 18px;
    font-family: 'Poppins', sans-serif;
    margin-top: 25px;
    box-shadow: 5px 5px 5px #240046;
    position: relative;
}

.bottom .left{
    margin-left: 20px;
}
.bottom .complete{
    margin-right: 20px;
}
.status div{
    display: inline-block;
    padding: 5px;    
}

@media screen and (max-width:767px) {
    .bottom{
        min-height: 50px;
    }
    .status{
        position: absolute;
        top: 70px;
        background-color: #7b2cbf;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 50px;
        box-shadow: 5px 5px 5px #240046;
    }
}
